<template>
<el-container>
  <el-header  height="50px">
    <base-head></base-head>
  </el-header>
  <el-container style="height:100%;">
      <base-sidebar class="main-sidear" ></base-sidebar>
    <el-main :class="['main',menuList.length>0?'showpad':'hidpad']">
      <!-- <base-notic class="main-notic"></base-notic> -->
      <base-breadcrumb></base-breadcrumb>
          <transition name="fade" mode="out-in">
             <router-view></router-view>
          </transition>
    </el-main>
  </el-container>
</el-container>
</template>

<script>
import BaseHead from "common/BaseHead.vue";
import BaseSidebar from "common/BaseSidebar.vue";
import BaseBreadcrumb from "common/BaseBreadcrumb.vue";
import BaseNotic from "common/BaseNotic.vue";
import { delAllCookie } from "assets/js/commonManage";
export default {
  components: {
    BaseHead,
    BaseSidebar,
    BaseBreadcrumb,
    BaseNotic
  },
  data() {
    return {};
  },
  mounted() {},
  created: function() {
    //在用户刷新网页后重新向客户端握手socket
    let userData = this.$getStore("userData");
    userData = JSON.parse(userData);
    this.$socket.emit("join", {
      userId: userData.id,
      menuList: userData.menuList
        .filter(value => {
          return value.parent_ids === "0,1,";
        })
        .map(value => {
          return value.permission;
        })
    });
  },

  computed: {
    menuList() {
      return this.$store.getters.getVerMenuMap;
    }
  },
  beforeDestroy() {
    window.localStorage.clear();
    delAllCookie();
    this.$store.dispatch("restVuexStore");
  }
};
</script>


<style lang="scss">
.el-header {
  padding: 0px !important;
}
.main-sidear {
  min-height: calc(100vh - 50px);
}
.main {
  position: relative;
  transition: all 0.4s;

  .main-notic {
    position: absolute;
    top: 0px;
    left: 0px;
  }
}
.showpad {
  padding: 5px 20px !important;
}
.hidpad {
  padding: 0px !important;
}
.sidebar {
  display: block;
  background-color: #545c64;
  // min-height: calc(100vh - 50px);
}
</style>



