<template>
  <div :style="headerStyle" class="base-header">

    <div class="header-logo">
      <img src="../../assets/images/logo.png" width="240" height="48">
    </div>
    <div @click="showMenu" class="header-menu-cll" title="折叠左侧菜单">
      <base-icon :class="['header-menu-default',isCollapse?'header-change':'']" icon="menu" />
    </div>
    <div class="header-notic" v-clickoutside="handleClose">
      <div class="header-notic-main" @click="noticClick">
        <el-badge :value="noticData.length">
          <base-icon icon="tongzhi" :class="datanew?'music':''" />
        </el-badge>
      </div>
      <transition name="animationsacle">
        <div class="header-notic-content" v-show="noticShow">

          <div class="header-notic-content-title">
            最新消息
          </div>
          <div style="height: 200px; overflow: auto;">
            <div class="header-notic-content-content" v-show="noticData.length>0" @click="readNotic(item)" v-for="(item,index) in noticData" :key="index">
              <div class="header-notic-img">
                <img :src="item.pic_path">
              </div>
              <div class="header-notic-text">
                <div style="overflow:hidden">
                  <div style="float:left;color:#3a8ee6">
                    {{item.username}}
                  </div>
                  <div style="float:right">{{item.create_date}}</div>
                </div>
                <div style="margin-top:5px;width:180px;">
                  <base-text-overflow :text="item.content" />
                </div>
              </div>
            </div>
            <div class="header-notic-content-no" v-show="noticData.length==0">
              没有新消息
            </div>
          </div>
          <!-- <div class="header-notci-conetnt-footer">
            <base-icon icon='menu'/>
            <span>全部消息</span>
          </div> -->
        </div>
      </transition>

    </div>

    <div class="header-set">
      <!-- <base-icon @click.native="logout" class="header-set-lout" icon="zuxiao"/> -->
      <div>
        <el-dropdown @command="handleCommand" trigger='click'>
          <div class="header-user-img" :title="userImg.realname">
            <img :src="setImg(userImg.pic_path)" width="50" height="50">
          </div>
          <el-dropdown-menu slot="dropdown">
            <!-- <el-dropdown-item command='0' >
              {{userImg.realname}}
            </el-dropdown-item> -->
            <el-dropdown-item command='1'>
              <span>
                <base-icon icon="zuxiao" />
              </span>
              <span style="margin-left:5px;">退出</span>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>

    </div>
    <div class="header-menu">
      <el-menu :default-active="defaultIndex" @select="selectIndex" class="menu-demo" mode="horizontal" background-color="#0e0e10" text-color="#ccc" active-text-color="#ffd04b">
        <el-menu-item v-for="(item,index) in menuData" class="header-menu-item" :key="item.id" :index="setIndex(index)">
          <!-- <base-icon icon="map" /> -->
          {{item.menu_name}}
        </el-menu-item>
      </el-menu>

    </div>
  </div>
</template>

<script>
import { transData, delAllCookie } from "assets/js/commonManage";
import BaseTextOverflow from "common/BaseTextOverflow";

export default {
  components: {
    BaseTextOverflow
  },
  data() {
    return {
      datanew: false,
      noticShow: false,
      defaultIndex: "",
      headerStyle: {
        backgroundColor: "#0e0e10"
      },
      userSrc: require("assets/images/default.jpg"),
      menuMap: [],
      menuData: [
        {
          menu_name: "菜单测试1"
        },
        {
          menu_name: "菜单测试2"
        },
        {
          menu_name: "菜单测试3"
        },
        {
          menu_name: "菜单测试4"
        },
        {
          menu_name: "菜单测试5"
        },
        {
          menu_name: "菜单测试6"
        },
        {
          menu_name: "菜单测试7"
        },
        {
          menu_name: "菜单测试8"
        },
        {
          menu_name: "菜单测试9"
        }
      ],
      transData: [],
      noticData: [], //消息推送
      testmenu: 123
    };
  },
  mounted() {
    let defalutIndex = JSON.parse(this.$getStore("horIndex"));
    this.menuMap = JSON.parse(this.$getStore("gMenuList")).filter(item => {
      return item.is_show === 1;
    });
    let list = this.menuMap.filter(item => {
      return item.menu_level === 0;
    });
    this.setSort(list);
    this.menuData = list;
    this.transData = transData(this.menuMap, "id", "parent_id", "children");
    if (defalutIndex != null) {
      this.defaultIndex = defalutIndex.toString();
      // let verlist = this.menuMap.filter(item => {
      //   return item.parent_id === this.menuData[defalutIndex].id;
      // });

      let verlist = this.transData.filter(item => {
        return item.id === this.menuData[defalutIndex].id;
      });

      let newData = verlist[0].children != undefined ? verlist[0].children : [];
      this.$store.dispatch("setVerMenuMap", newData);
      console.log(this.transData);
      //  this.$setStore("verMap", verlist); //保存选中横向菜单后子菜单信息
    }

    let notic = JSON.parse(this.$getStore("notifyData"));
    if (notic != null) {
      this.noticData = notic; //获取缓存中的消息推送
    }
  },
  methods: {
    handleClose() {
      this.noticShow = false;
    },

    /**
     * 设置推送消息以阅读
     */
    async readNotic(data) {
      console.log(data);
      const Res = await this.$http.post(
        "/oa/OaNotifyController/updateNotifyRecord",
        { id: data.id }
      );
      console.log(Res);
      if (Res.data.success) {
        this.noticData = this.noticData.filter(item => {
          return item.id != data.id;
        });

        let menuId = this.menuMap.map(item => {
          if (item.menu_url == data.menuUrl) {
            return item.parent_id;
          }
        });

        this.$router.push({
          path: data.menuUrl,
          query: {
            title: data.title
          }
        });
        this.$store.dispatch("setVerDefaultIndex", data.menuUrl);
        console.log(this.noticData);
      }
    },
    /**
     * 显示消息推送列表
     */
    noticClick() {
      this.noticShow = this.noticShow ? false : true;
    },
    setImg(img) {
      let newImg = this.userSrc;
      if (img) {
        newImg =
          "http://" + this.$webconfig.serverIp + "/" + img + ".100x100.png";
      }
      return newImg;
    },
    handleCommand(command) {
      let type = parseInt(command);
      switch (type) {
        case 1: //退出登录
          this.logout();
          break;

        default:
          break;
      }
    },
    /**
     * 注销登录
     */
    logout() {
      window.localStorage.clear();
      delAllCookie();
      // this.$router.push("/Login");
      location.reload(); // 为了重新实例化vue-router对象 避免bug

      // this.$store.dispatch("restVuexStore");
    },
    /**
     * 设置菜单折叠状态
     */
    showMenu() {
      this.$store.dispatch("setIsCollapse");
    },
    /**
     * 排序
     */
    setSort(list) {
      list.sort(function(a, b) {
        return a.menu_order - b.menu_order;
      });
    },
    selectIndex(index, indexPath) {
      console.log(index, indexPath);
      let verlist = this.transData.filter(item => {
        return item.id === this.menuData[index].id;
      });

      let newData = verlist[0].children != undefined ? verlist[0].children : [];
      this.$store.dispatch("setVerMenuMap", newData);
      // let list = this.menuMap.filter(item => {
      //   return item.parent_id === this.menuData[index].id;
      // });
      // this.setSort(list);
      this.$setStore("horIndex", index); // 保存横向导航选择菜单项
      // // this.$setStore("verMap", list); //保存选中横向菜单后子菜单信息
      if (verlist[0].children === undefined) {
        this.$router.push(verlist[0].menu_url);
      }
      // this.$store.dispatch("setVerMenuMap", list);
    },
    /**
     * 设置激活状态
     */
    setIndex(index) {
      return index.toString();
    },
    async getNoticUserInfo(data) {
      const Res = await this.$http.get("/sys/UserController/getUserInfoById", {
        params: {
          id: data.create_by
        }
      });
      console.log(Res);
      if (Res.data.success) {
        let newData = Res.data.result;
        data.username = newData.username;
        let img = this.userSrc;
        console.log("newda", newData);
        if (newData.pic_path != null) {
          data.pic_path = `http://${this.$webconfig.serverIp}/${
            newData.pic_path
          }.100x100.png`;
        } else {
          data.pic_path = img;
        }
        console.log(data);
        this.noticData.push(data);

        this.setNotcinShow();
      }
    },
    setNotcinShow() {
      let audio = new Audio("../../../static/msg/msg.mp3");
      audio.play();
      this.datanew = true;
      setTimeout(() => {
        this.datanew = false;
      }, 1000);
    }
  },
  computed: {
    isCollapse() {
      return this.$store.getters.getisCollapse;
    },
    userImg() {
      const userData = JSON.parse(this.$getStore("userData"));
      return userData;
    },
    notic() {
      return this.$store.getters.getSocketMsgByUser;
    }
  },
  watch: {
    notic(val) {
      console.log("消息推送：", val);
      if (val.moduleName === "xtbg") {
        console.log(val.userIds[0], this.userImg.id);
        if (val.userIds[0] === this.userImg.id) {
          let data = val.result.notify;
          data.menuUrl = val.menuUrl;
          this.getNoticUserInfo(data);
        }
      }
    },
    noticData(val) {
      this.$setStore("notifyData", val);
    }
  }
};
</script>


<style lang="scss">
.base-header {
  // overflow: hidden;
  height: 50px;
  position: relative;
  .header-menu {
    height: 50px; // width: 1200px;
    font-size: 30px;
    color: #ccc;
    padding: 0 10px;
    line-height: 50px;
    overflow: hidden;
    // overflow-x: scroll;
    // overflow-y: hidden;
    &:hover {
      cursor: pointer;
      color: #fff;
    }

    .is-active {
      background: #2c2c2e !important;
    }
  }
  .header-logo {
    float: left;
    width: 250px;
    height: 50px;
    background-color: #0e0e10;
  }
  .header-menu-cll {
    height: 50px;
    float: left;
    line-height: 50px;
    padding: 0 10px;
    color: #ccc;
    font-size: 30px;
    &:hover {
      cursor: pointer;
      color: #fff;
    }
    .header-menu-default {
      margin-left: 5px;
      transition: all 0.3s;
    }
    .header-change {
      transform: rotate(-90deg);
    }
  }
  .header-menu {
    // float: left;
    position: absolute;
    top: 0px;
    left: 305px; // width: calc(100vw - 321px);
    height: 50px; // width: 1200px;
    .menu-demo {
      .el-menu-item {
        height: 50px !important;
        line-height: 50px !important;
        &:hover {
          background-color: #2c2c2e !important;
        }
      }
      .el-menu-item.is-active {
        // border-bottom: 4px solid #409eff;
      }
    }
  }
  .header-set {
    background-color: #0e0e10;
    height: 50px;
    /* height: 50px;
      line-height: 50px; */
    // width: 100px;
    font-size: 40px;
    position: absolute;
    top: 0px;
    right: 10px;
    z-index: 10;
    overflow: hidden;
    .header-user-img {
      // width: 50px;
      // height: 50px;
      // background-color: red;
      // border-radius: 50%;
      // position: relative;
      img {
        // position: absolute;
        // top: -40px;
        margin-top: 5px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
      }
    }
    .header-set-lout {
      float: right;
      height: 50px;
      line-height: 50px;
      font-size: 35px;
      margin-right: 10px;
      color: #ccc;
      &:hover {
        cursor: pointer;
        color: #f56c6c;
      }
    }
  }
  .header-notic {
    color: rgb(204, 204, 204);
    position: absolute;
    top: 0px;
    z-index: 20;
    right: 65px;
    padding: 0 5px;
    font-size: 30px;
    &:hover {
      cursor: pointer;
      background-color: #2c2c2e !important;
    }
    .header-notic-main {
      position: relative; // height: 50px;
      margin-top: 6px; // line-height: 50px;
      .el-badge__content {
        border: 0px !important;
        padding: 0 4px !important;
      }
      .is-fixed {
        top: 10px;
        right: 12px;
      }
    }
    .header-notic-content {
      font-size: 14px;
      position: absolute;
      top: 50px;
      right: 0px;
      color: #444;
      background-color: #fff;
      z-index: 99999;
      width: 250px;

      -webkit-box-shadow: 0 3px 12px rgba(0, 0, 0, 0.05);
      box-shadow: 0 3px 12px rgba(0, 0, 0, 0.05);
      transition: all 0.4s;
      border: 1px solid rgba(130, 128, 128, 0.2);
      .header-notic-content-title {
        padding: 5px 15px;
        font-size: 14px;
        border-bottom: 1px solid rgba(130, 128, 128, 0.2);
      }

      .header-notic-content-content {
        padding: 5px 10px;
        display: flex;
        .header-notic-img {
          flex: 0.2;
          img {
            width: 35px;
            height: 35px;
            border-radius: 50%;
          }
        }
        .header-notic-text {
          width: 180px;
          padding-left: 15px;
          font-size: 12px;
          flex: 1;
        }
        &:hover {
          background-color: rgba(204, 204, 204, 0.35);
        }
      }
      .header-notic-content-no {
        text-align: center;
        padding-top: 90px;
        color: #a0a0a0;
      }
      .header-notci-conetnt-footer {
        border-top: 1px solid rgba(130, 128, 128, 0.2);
        padding: 5px 15px;
        color: #ccc;
        &:hover {
          color: #409eff;
        }
      }
    }
    .animationsacle-enter-active {
      transform-origin: 100% 0;
      animation: noticScaleIn 0.4s 0s 1 both;
    }
    .animationsacle-leave-active {
      transform-origin: 100% 0;
      animation: noticScaleOut 0.3s 0s 1 both;
    }
    .music {
      animation: headShake 1s 0s 1 both;
    }
    @keyframes headShake {
      0% {
        transform: translateX(0);
      }

      6.5% {
        transform: translateX(-6px) rotateY(-9deg);
      }

      18.5% {
        transform: translateX(5px) rotateY(7deg);
      }

      31.5% {
        transform: translateX(-3px) rotateY(-5deg);
      }

      43.5% {
        transform: translateX(2px) rotateY(3deg);
      }

      50% {
        transform: translateX(0);
      }
    }
    @keyframes noticScaleIn {
      0% {
        opacity: 0;
        -webkit-transform: scale(0.2);
        transform: scale(0.2);
      }
      100% {
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
      }
    }
    @keyframes noticScaleOut {
      from {
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
      }
      to {
        opacity: 0;
        -webkit-transform: scale(0);
        transform: scale(0);
      }
    }
  }
}
</style>



