import Vue from 'vue'
import Vuex from 'vuex'
import {
  state
} from './state'
import * as mutations from './mutations'
import * as actions from './actions'
// import * as getters from './getters'
import * as getters from './getters'
Vue.use(Vuex)


/**
 * state
 * state是vuex自己维护的一份状态数据
 */



export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations,
})



