<template>
  <!-- <div v-show="menuList.length>0" :class="['sidebar-content',menuList.length>0?'':'']"> -->
  <div :class="['sidebar-content',menuList.length>0?'':'sidebar-content-hide']">
    <el-menu class="el-menu-vertical-demo" :collapse="isCollapse" :default-active="defaultIndex" @select="selectIndex" router
      unique-opened background-color="#0e0e10" text-color="#ccc" active-text-color="#ffd04b">
      <!-- <div v-for="(item,index) in menuList" :key="item.id"> -->
      <el-submenu v-for="(item,index) in menuList" :key="item.id" v-if="item.children" :index="setIndex(index)">
        <template slot="title">
          <base-icon :class="['sidebar-icon',isCollapse?'sidebar-icon-chang':'']" :icon="item.menu_icon" />
          <span>{{item.menu_name}}</span>
        </template>
        <el-menu-item v-for="(item2,index2) in item.children" :key="item2.id" :index="item2.menu_url">
          {{item2.menu_name}}
        </el-menu-item>
      </el-submenu>
      <el-menu-item v-else :index="item.menu_url">
        <base-icon :class="['sidebar-icon',isCollapse?'sidebar-icon-chang':'']" :icon="item.menu_icon" />
        <span slot="title">{{item.menu_name}}</span> 
      </el-menu-item>
      <!-- </div> -->

    </el-menu>
  </div>
</template>


<script>
import { mapGetters } from "vuex";
export default {
  data: function() {
    return {
      // isCollapse: false,
      defaultIndex: "",
      menuData: [
        {
          menu_name: "菜单测试1"
        },
        {
          menu_name: "菜单测试2"
        },
        {
          menu_name: "菜单测试3"
        },
        {
          menu_name: "菜单测试4"
        },
        {
          menu_name: "菜单测试5"
        },
        {
          menu_name: "菜单测试6"
        },
        {
          menu_name: "菜单测试7"
        },
        {
          menu_name: "菜单测试8"
        },
        {
          menu_name: "菜单测试9"
        }
      ]
    };
  },
  mounted() {
    let verIndex = this.$getStore("verIndex");
    if (verIndex != null) {
      this.$nextTick(function() {
        this.defaultIndex = verIndex;
      });
    }
  },
  methods: {
    /**
       * 设置激活状态
       */
    setIndex(index) {
      return index.toString();
    },
    selectIndex(index, indexPath) {
      // let obj={
      //   horIndex:this.$getStore('horIndex'),
      //   verIndex:index
      // }
      // console.log(obj);
      this.$setStore("verIndex", index); // 保存横向导航选择菜单项
    }
  },
  watch: {
    compIndex(val){
      this.defaultIndex=val;
    }
  },
  computed: {
    compIndex(){
      return this.$store.getters.getVerDefaultIndex;
    },
    isCollapse() {
      return this.$store.getters.getisCollapse;
    },
    menuList() {
      return this.$store.getters.getVerMenuMap;
    },
    theme() {
      return this.$store.getters.getTheme;
    },
    onRoutes() {
      return this.$route.path.replace("/", "");
    },
    navMenuList() {
      return this.$store.getters.getnavList;
    },
    navType() {
      return this.$store.getters.getNavType;
    }
  }
};
</script>



<style lang="scss">
.sidebar-content {
  border-right: solid 1px #e6e6e6;
  transition: all 1s;
  /* height: 100vh; */
  background-color: #0e0e10;
  .sidebar-icon {
    font-size: 18px;
    margin: 0 10px 0 10px;
    transition: all 0.4s;
  }
  .sidebar-icon-chang {
    font-size: 23px;
    margin: 0 10px 0 0px;
  }
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px ;
  }
  .el-menu {
    border-right: 0px !important;
  }
  .el-menu-item{
    &:hover{
            background: #2c2c2e !important
    }
  }
}
.sidebar-content-hide {
  width: 0px;
}

</style>



